import React, { useEffect, useState } from 'react';
import { SRLWrapper } from 'simple-react-lightbox';
import images from './DataImg';
// import { NavLink } from 'react-router-dom'

const Gallery = () => {
    const [tag, setTag] = useState('All');
    const [filteredImages, setFilteredImages] = useState([]);

    useEffect(
        () => {
            tag === 'All' ? setFilteredImages(images) : setFilteredImages(images.filter(image => image.tag === tag));
        },
        [tag]
    );

    const TagButton = ({ name, handleSetTag, tagActive }) => {
        return (
            <button className={`tag ${tagActive ? 'active' : null}`} onClick={() => handleSetTag(name)}>
                {name}
            </button>
        );
    };

    return (
        <div>
            <br />
            <section className="container my-5 py-4">
            <div className="tags">
                <h4>Exclusive Trains Brochure</h4>
                <p>MÁV Nostalgia - Presentation Publication</p>
                <TagButton name="All" tagActive={tag === 'All' ? true : false} handleSetTag={setTag} /> |
                <TagButton name="Covers" tagActive={tag === 'Covers' ? true : false} handleSetTag={setTag} /> |
                <TagButton name="Brochure pages" tagActive={tag === 'Brochure pages' ? true : false} handleSetTag={setTag} />
            </div>
            <SRLWrapper>
                <div className="cont">
                    {filteredImages.map(image => image.galleryName === 'Exclusive Trains' ? (
                        <div key={image.id} className="image-card">
                            <a href={`${image.imageName}`}>
                                <img className="image" src={`${image.imageName}`} alt="" />
                            </a>
                        </div>
                    ) : (
                        <div></div>
                    ))}
                </div>
            </SRLWrapper>
            <br />



            <div className="tags">
                <h4>House History Exhibition</h4>
                <p>Budapest100 - "In the Footsteps of Bauhaus" - Barát utca - Budapest</p>
                <TagButton name="All" tagActive={tag === 'All' ? true : false} handleSetTag={setTag} /> |
                <TagButton name="Covers" tagActive={tag === 'Covers' ? true : false} handleSetTag={setTag} /> |
                <TagButton name="Brochure pages" tagActive={tag === 'Brochure pages' ? true : false} handleSetTag={setTag} />
            </div>
            <SRLWrapper>
                <div className="cont">
                    {filteredImages.map(image => image.galleryName === 'Bp100 Barat' ? (
                        <div key={image.id} className="image-card">
                            <a href={`${image.imageName}`}>
                                <img className="image" src={`${image.imageName}`} alt="" />
                            </a>
                        </div>
                    ) : (
                        <div></div>
                    ))}
                </div>
            </SRLWrapper>
            <br />


    


                {/* <div className="row">
                    <div className="col-md-4">
                        <div className="thumbnail d-flex justify-content-center align-items-center">
                            <NavLink className="nav-link" to="./contact">
                                <img src={require("../img/nyugati.JPG").default} alt="nyugati-station" width="300px" />
                                <div className="caption text-center py-5">
                                    <p className="card-text">Project 1</p>
                                </div>
                            </NavLink>
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="thumbnail d-flex justify-content-center align-items-center">
                            <NavLink className="nav-link" to="./contact">
                            <img src={require("../img/teakwood.JPG").default} alt="teakwood-diner" width="300px" />
                                <div className="caption text-center py-5">
                                    <p className="card-text">Project 2</p>
                                </div>
                            </NavLink>
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="thumbnail d-flex justify-content-center align-items-center">
                            <NavLink className="nav-link" to="./contact" activeStyle={{ color: 'red' }}>
                            <img src={require("../img/istvantelek.JPG").default} alt="istvantelek" width="300px" />
                                <div className="caption text-center py-5">
                                    <p className="card-text">Project 3</p>
                                </div>
                            </NavLink>
                        </div>
                    </div>
                </div> */}
            </section>
        </div>
    )
}

export default Gallery
