import React from 'react'
import DATA from './Data'
import { NavLink } from 'react-router-dom'

const Service = () => {
    const card = (props) => {
        return (
            <div className="col-md-4 mb-5 pb-5" key={props.id}>
                <NavLink className="nav-link" to={props.link}>
                    <div className="card text-center py-5 d-flex justify-content-center align-items-center">
                        <img src={props.img} className="card-img-top mx-auto p-2" alt={props.title} />
                        <div className="card-body">
                            <h5 className="card-title">{props.title}</h5>
                            <p className="card-text">{props.desc}</p>
                        </div>
                    </div>
                </NavLink>
            </div>
        );
    }
    return (
        <div>
            <div className="container">
                <div className="row">
                    <div className="col-12 text-center tag py-4 mb-5">
                        <h4>Services</h4>
                    </div>
                    <div className="row">
                        {DATA.map(card)}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Service
